<template>
  <div class="MultiPrintOption">
    <v-dialog v-model="dialog" width="30vw">
      <template v-slot:activator="{ on }">
        <v-btn class="success ml-2" v-on="on" @click="enterProcess" v-show="printItems.length <= 5">列印表單選擇</v-btn>
      </template>

      <v-card>
        <v-card-title :class="titleClass" primary-title>{{title}}</v-card-title>

        <v-card-text>
          <v-alert color="primary" outlined class="mt-2">
            <div class="text-right">
              <v-btn class="primary mr-1" @click="selectAll">全選</v-btn>
              <v-btn color="primary" outlined @click="noAll">全不選</v-btn>
            </div>

            <v-checkbox label="住民定期評估表" v-model="list.record" value="true" @click.stop="objcheck"></v-checkbox>
            <v-checkbox label="柯氏/巴氏(ADL)量表" v-model="list.kb" value="true" @click.stop="objcheck"></v-checkbox>
            <v-checkbox label="跌倒危險因子評估表" v-model="list.fall" value="true" @click.stop="objcheck"></v-checkbox>
            <v-checkbox
              label="Braden壓力性損傷風險評估表"
              v-model="list.braden"
              value="true"
              @click.stop="objcheck"
            ></v-checkbox>
            <v-checkbox
              label="簡易心智狀態SPMSQ"
              v-model="list.spmsq"
              value="true"
              @click.stop="objcheck"
            ></v-checkbox>
          </v-alert>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>

          <v-btn class="primary" @click="printOK" v-show="objcheck()">確認列印</v-btn>
        </v-card-actions>
        <!-- <pre>{{printItems}}</pre> -->
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import qs from "qs";
import dayjs from "dayjs";

export default {
  props: ["printItems"],
  data() {
    return {
      dialog: false,
      list: {
        record: "",
        kb: "",
        fall: "",
        braden: "",
        spmsq:""
      },
      emptyRules: [v => !!v || "不可空白"],
      title: "", //抬頭
      titleClass: "", //抬頭顯示樣式

      recordItems : [],
      kbItems : [],
      fallItems : [],
      bradenItems : [],
      spmsqItems : [],

      documents : ["record","kb","fall","braden","spmsq"],  //設定複合列印的表單名
    };
  },

  mounted() {
    //父組件要執行本組件時的function時的設定 (showFromParent) id是暫存傳過來的值
    this.$on("childFn", function() {
      this.dialog = true;
      this.enterProcess();
    });
    this.selectAll();
  },

  methods: {
    //確認列印
    printOK() {
      this.$confirm("確認顯示列印畫面?").then(res => {
        if (res) {
          var printData = {}

          printData['matchDate'] = this.printItems.map(i=>{
            return i.date
          })
          var matchStr = JSON.stringify(printData['matchDate'].sort());
          
          this.documents.forEach(document=>{
            printData[document] = this[document+'Items'].filter(i=>{
            var newItem = JSON.parse(i.datalist);
            return matchStr.includes(newItem.date);
          });
          })  

          printData['choiceDocuments'] = this.list;
          console.log(printData);
          
          // //送出資料到列印頁面
          sessionStorage.setItem("printData", JSON.stringify(printData));
          let routeData = this.$router.resolve({
            path: "/print/multidocumentprint"
          });
          window.open(routeData.href, "_blank");
        }
      });
    },
    //判斷是否全部都沒選 都沒選的時候.總長度是21
    objcheck() {
      if (JSON.stringify(Object.values(this.list)).length > 21) {
        return true;
      } else {
        return false;
      }
    },
    enterProcess(item) {
      //執行修改流程(本案只拿來顯示畫面,透過父項目的控制)
      this.title = "列印表單選擇";
      this.titleClass = "font-weight-black primary lighten-2";

      //取得四個表裡面,同一個病歷的資料
      this.documents.forEach(document=>{
        this.getItems(document);
      })
    },
    getItems(document) {
      var postData = {
        key: "u_snkey",
        value: this.$store.state.uData.snkey
      };

      //取得所有資料
      //this.$store.commit("setLoadingOn"); //讀取畫面on
      
      this.axios
        .post(
          "general/getByKey/" + this.$store.state.databaseName + "/" + document,
          qs.stringify(postData)
        )
        .then(rs => {
          this[document+'Items'] = rs.data;
          // this.$store.commit("setLoadingOff"); //讀取畫面off
        });
    },
    //全選
    selectAll() {
      for (var i in this.list) {
        this.list[i] = "true";
      }
      this.objcheck();
    },
    //全不選
    noAll() {
      for (var i in this.list) {
        this.list[i] = "";
      }
      this.objcheck();
    }
  }
};
</script>

