<template>
  <div class="kbadd">
    <v-dialog v-model="dialog" width="auto">
      <template v-slot:activator="{ on }">
        <v-icon color="red lighten-2" dark v-on="on" @click="addProcess">mdi-plus-circle</v-icon>
      </template>

      <v-card>
        <v-card-title :class="titleStyle" primary-title>{{title}}</v-card-title>

        <v-card-text>
          <v-form ref="form" class="mt-3">
            <table class="table table-bordered table-sm bg-light">
              <tr>
                <td class="form_title align-middle" colspan="4">評估日期</td>
                <td>
                  <v-text-field type="date" v-model="list.date" :rules="emptyRules"></v-text-field>
                </td>
              </tr>
              <!-- 入住適應暨心理社會評估 -->
              <tr>
                <td class="form_title">
                  <span class="vertical-mode">柯氏量表</span>
                </td>
                <td colspan="3">
                  0級:完全活動,不受任何限制。
                  <br />1級:能夠步行及維持輕度工作。如:簡單之家務。
                  <br />2級:能夠步行及維持大部分自我照顧。但無法進行家務。
                  <br />3級:只能維持有限的自我照顧，50％以上的清醒時間，活動限於在床或椅上。
                  <br />4級:完全無活動，不能進行任何自我照顧，且完全限制在床或椅。
                </td>
                <td>
                  <select class="form-control" id="klv" name="klv" v-model="list.klv">
                    <option>0級</option>
                    <option>1級</option>
                    <option>2級</option>
                    <option>3級</option>
                    <option>4級</option>
                  </select>
                </td>
              </tr>
              <tr>
                <td class="form_title" rowspan="11">
                  <span class="vertical-mode">日常生活功能(巴氏量表)ADL</span>
                </td>
                <td>1</td>
                <td>進食</td>
                <td>
                  10：自己在合理時間內(約十秒鐘吃一口)，可用筷子取食眼前的食物。若需進食輔具時，應會自行穿脫。
                  <br />5：需別人幫忙穿脫進食輔具或只會用湯匙進食。
                  <br />0：無法自行取食或耗費時間過長。
                </td>
                <td>
                  <select class="form-control" id="eat" name="eat" v-model="list.eat">
                    <option>10</option>
                    <option>5</option>
                    <option>0</option>
                  </select>
                </td>
              </tr>
              <tr>
                <td>2</td>
                <td>個人衛生</td>
                <td>
                  5：可自行洗手、刷牙、洗臉及梳頭。
                  <br />0：需要他人部分或完全協助。
                </td>
                <td>
                  <select class="form-control" id="hygiene" name="hygiene" v-model="list.hygiene">
                    <option>5</option>
                    <option>0</option>
                  </select>
                </td>
              </tr>
              <tr>
                <td>3</td>
                <td>上廁所</td>
                <td>
                  10：可自行上下馬桶、穿脫衣服、不會弄髒衣服、會自行使用衛生紙擦拭。
                  <br />5：需要協助保持姿勢的平衡、整理衣服或用衛生紙
                  <br />0：無法自己完成。
                </td>
                <td>
                  <select class="form-control" id="wc" name="wc" v-model="list.wc">
                    <option>10</option>
                    <option>5</option>
                    <option>0</option>
                  </select>
                </td>
              </tr>
              <tr>
                <td>4</td>
                <td>洗澡</td>
                <td>
                  5：能獨立完成(不論是盆浴或沐浴),不需別人在旁。
                  <br />0：需別人協助。
                </td>
                <td>
                  <select class="form-control" id="bathing" name="bathing" v-model="list.bathing">
                    <option>5</option>
                    <option>0</option>
                  </select>
                </td>
              </tr>
              <tr>
                <td>5</td>
                <td>穿脫衣服</td>
                <td>
                  10：可自己穿脫衣服、鞋子,自己扣釦子、上拉鍊或綁鞋帶。
                  <br />5：在別人協助下,可自行完成一半以上動作。
                  <br />0：不會自己做
                </td>
                <td>
                  <select class="form-control" id="wear" name="wear" v-model="list.wear">
                    <option>10</option>
                    <option>5</option>
                    <option>0</option>
                  </select>
                </td>
              </tr>
              <tr>
                <td>6</td>
                <td>大便控制</td>
                <td>
                  10：不會失禁，能自行灌腸或使用塞劑。
                  <br />5：偶爾會失禁(每週不超過一次)，需要他人協助使用灌腸或塞劑。
                  <br />0：失禁,無法自己控制且需他人處理。
                </td>
                <td>
                  <select class="form-control" id="stool" name="stool" v-model="list.stool">
                    <option>10</option>
                    <option>5</option>
                    <option>0</option>
                  </select>
                </td>
              </tr>
              <tr>
                <td>7</td>
                <td>小便控制</td>
                <td>
                  10：能自行控制不會有失禁,或能自行使用並清潔尿套、尿袋。
                  <br />5：偶爾會失禁(每週不超過一次)，或尿急(無法等待放好便盆或及時趕到廁所)或需要他人協助處理尿套。
                  <br />0：失禁,無法自己控制且需他人處理。
                </td>
                <td>
                  <select class="form-control" id="pee" name="pee" v-model="list.pee">
                    <option>10</option>
                    <option>5</option>
                    <option>0</option>
                  </select>
                </td>
              </tr>
              <tr>
                <td>8</td>
                <td>平地行走</td>
                <td>
                  15：使用或不使用輔具,皆可獨立行走50公尺以上。
                  <br />10：需他人稍微扶持或口頭指導才能行走50公尺以上。
                  <br />5：雖無法行走，但可獨立操縱輪椅(包括轉彎、進門及接近桌子、床沿)並可推行輪椅50公尺以上。
                  <br />0：完全無法行走,需別人幫忙推輪椅。
                </td>
                <td>
                  <select class="form-control" id="walk" name="walk" v-model="list.walk">
                    <option>15</option>
                    <option>10</option>
                    <option>5</option>
                    <option>0</option>
                  </select>
                </td>
              </tr>
              <tr>
                <td>9</td>
                <td>上下樓梯</td>
                <td>
                  10：可自行上下樓梯。可使用扶手、枴杖等輔具。
                  <br />5：需他人協助或監督才能上下樓梯。
                  <br />0：無法上下樓梯。
                </td>
                <td>
                  <select class="form-control" id="stairs" name="stairs" v-model="list.stairs">
                    <option>10</option>
                    <option>5</option>
                    <option>0</option>
                  </select>
                </td>
              </tr>
              <tr>
                <td>10</td>
                <td>上下床或椅子</td>
                <td>
                  15：整個過程可獨立完成。
                  <br />10：移動身體時需要稍微協助、給予提醒、安全監督。
                  <br />5：可以自行坐起,但從床上坐起時移動身體時需要他人協助。
                  <br />0：不會自己移動。
                </td>
                <td>
                  <select class="form-control" id="bed" name="bed" v-model="list.bed">
                    <option>15</option>
                    <option>10</option>
                    <option>5</option>
                    <option>0</option>
                  </select>
                </td>
              </tr>
              <tr>
                <td colspan="2">0-20:完全依賴 21-60:嚴重依賴 61-90:中度依賴 91-99:輕度依賴 100:獨立</td>
                <td>總分</td>
                <td>
                  <input type="text" class="form-control text-danger" :value="total" readonly />
                </td>
              </tr>
            </table>
          </v-form>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn class="primary" @click="addOK" v-if="processType=='add'" :loading="loading" :disabled="loading">確認新增</v-btn>
          <v-btn class="success" @click="editOK" v-if="processType=='edit'" :loading="loading" :disabled="loading">確認修改</v-btn>
        </v-card-actions>
        <!-- <pre>{{list}}</pre> -->
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import qs from "qs";

export default {
  data() {
    return {
      dialog: false,
      list: {}, //表單內資料
      oldList: {}, //未修改前的表單內資料
      processType: "", //存放頁面執行是add新增或edit修改
      title: "",
      titleStyle: "",
      emptyRules: [v => !!v || "不可空白"],
      loading:false,
    };
  },
  props: ["viewData"],
  mounted() {
    //父組件要執行本組件時的function時的設定 (showFromParent) id是暫存傳過來的值
    this.$on("fromParent", function(item) {
      this.dialog = true;
      this.editProcess(item);
    });
  },
  
  computed: {
    total() {
      this.list.total = (
        parseInt(this.list.eat) +
        parseInt(this.list.hygiene) +
        parseInt(this.list.wc) +
        parseInt(this.list.bathing) +
        parseInt(this.list.wear) +
        parseInt(this.list.stool) +
        parseInt(this.list.pee) +
        parseInt(this.list.walk) +
        parseInt(this.list.stairs) +
        parseInt(this.list.bed)
      );
      return this.list.total;
    }
  },
  methods: {
    addProcess() {
      //進入畫面時,執行的是新增作業
      this.processType = "add";
      this.title = "新增資料";
      this.titleStyle = "font-weight-black error lighten-2";
      // this.list = {};

      //設定最後一筆資料為新增時的預設值及清除新增中不必要的值
      if (this.viewData[0] != undefined  ){
        this.list = { ...this.viewData[0] };
      }
      this.list.snkey = "";
      this.list.date = "";
      this.list.edit_man = "";
    },
    editProcess(item) {
      //進入畫面時,執行的是修改作業
      this.processType = "edit";
      this.title = "修改資料";
      this.titleStyle = "font-weight-black success lighten-2";
      this.oldList = item; //將頁面資料與父項的資料連結
      this.list = { ...item }; //複制一份實際修改資料內容
    },
    addOK() {
      //確認新增
      if (this.$refs.form.validate()) {
        this.$store.commit("getTime"); //取得現在時間
        this.list.u_snkey = this.$store.state.uData.snkey;
        this.list["create_man"] = this.$store.state.pData.username + "(" + this.$store.state.time + ")"; //執行動作的人,時間,內容

        var postData = {
          u_snkey : this.list.u_snkey,
          datalist : JSON.stringify(this.list),
          create_man : this.list.create_man
        }

        this.loading = true;
        this.axios
          .post(
            "general/add/" + this.$store.state.databaseName + "/kb",
            qs.stringify(postData)
          )
          .then(rs => {
            // console.log(rs.data)
            if (rs.data.state == 1) {
              var pop = { msg: "已新增", type: true, theme: "error" };
              this.$store.commit("snackbar", pop);
              this.list = {};
              this.dialog = false;
              this.$emit("getAllData");
              this.loading = false;
            }
          });
      } else {
        var pop = {
          msg: "資料輸入不完全!!請重新確認!!",
          type: true,
          theme: "warning"
        };
        this.$store.commit("snackbar", pop);
      }
    },
    editOK() {
      //確認修改
      if (this.$refs.form.validate()) {
        this.$store.commit("getTime"); //取得現在時間
        // this.list.u_snkey = this.$store.state.uData.snkey;
        this.list["edit_man"] = this.$store.state.pData.username + "(" + this.$store.state.time + ")"; //執行動作的人,時間,內容

        var postData = {
          snkey : this.list.snkey,
          datalist : JSON.stringify(this.list),
          edit_man : this.list.edit_man
        }

        this.loading = true;
        this.axios
          .post(
            "general/edit/" + this.$store.state.databaseName + "/kb",
            qs.stringify(postData)
          )
          .then(rs => {
            if (rs.data.state == 1) {
              var pop = { msg: "已修改", type: true, theme: "success" };
              this.$store.commit("snackbar", pop);
              this.dialog = false;

              for (var i in this.list) {
                //將各個欄位的資料透過一對一的去比對,就可以使vue對資料會有反應
                this.oldList[i] = this.list[i];
              }
              this.loading = false;
            }
          });
      }
    }
  }
};
</script>

<style lang="scss" scoped>
@import "node_modules/bootstrap/scss/bootstrap";
@import "node_modules/bootstrap-vue/src/index.scss";

.form_title {
  font-size: 1.2rem;
  text-align: center;
}
.vertical-mode {
  writing-mode: tb-rl;
  -webkit-writing-mode: vertical-rl;
  writing-mode: vertical-rl;
}
</style>