<template>
  <div class="kblist">
    <v-card class="mt-2">
      <v-card-title>
        <v-row>
          <v-col cols="12" md="8">
            <h2 class="subheading grey--text">
              <v-icon class="mx-1">fas fa-file-alt</v-icon>
              <span>
                {{this.$store.state.uData.p_code}} /
                {{this.$store.state.uData.name}}
              </span>
              <span>柯氏/巴氏(ADL)量表</span>
            </h2>
          </v-col>
          <v-col cols="12" md="4">
            <!-- 功能表 -->
            <v-toolbar dense>
              <v-text-field
                hide-details
                prepend-icon="mdi-magnify"
                single-line
                label="搜尋"
                v-model="serachKey"
                @keyup.stop="search()"
              ></v-text-field>

              <popupadd
                :viewData="viewData"
                @getAllData="getAllData"
                ref="editProcess"
              ></popupadd>
              <MultiPrintSelect :printChoice="printChoice" :printItems="printItems" @changeChoice="changeChoice"></MultiPrintSelect>
              <!-- <v-btn icon @click.stop="print">
            <router-link :to="{name:'printmember'}" target="_blank" style="text-decoration:none;">
              <v-icon color="success">mdi-printer</v-icon>
            </router-link>
              </v-btn>-->
            </v-toolbar>
          </v-col>
        </v-row>
      </v-card-title>
      <v-card-text>
        <v-card class="mt-2">
          <v-card-text>
            <!-- 表單內容 -->
            <v-data-iterator
              :items="viewData"
              :items-per-page.sync="itemsPerPage"
              :footer-props="{ itemsPerPageOptions }"
            >
              <template v-slot:default="props">
                <v-simple-table fixed-header class="mt-2 text-no-wrap">
                  <template v-slot:default>
                    <thead class="title">
                      <tr>
                        <th rowspan="3"></th>
                        <th rowspan="2">評估日期</th>
                        <th rowspan="2">柯氏量表</th>
                        <th colspan="11">日常生活功能(巴氏量表)ADL</th>
                        <th rowspan="2" class="ced_man_key">紀錄人</th>
                        <th rowspan="2" class="ced_man_key">修改紀錄</th>
                      </tr>
                      <tr>
                        <th>1.進食</th>
                        <th>2.個人衛生</th>
                        <th>3.上廁所</th>
                        <th>4.洗澡</th>
                        <th>5.穿脫衣服</th>
                        <th>6.大便控制</th>
                        <th>7.小便控制</th>
                        <th>8.平地行走</th>
                        <th>9.上下樓梯</th>
                        <th>10.上下床或椅子</th>
                        <th>總分</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr v-for="(item,index) in props.items" :key="index">
                        <td class="d-flex align-center">
                          <v-checkbox label="列印選擇" v-model="item.check" v-if="!printChoice" @click.stop="filterPrintItem"></v-checkbox>
                          <v-menu transition="scale-transition" offset-y v-if="printChoice">
                            <template v-slot:activator="{ on }">
                              <v-btn color="primary" dark v-on="on">功能</v-btn>
                            </template>
                            <v-list>
                              <v-list-item @click="edit(item)">
                                <v-list-item-title>修改</v-list-item-title>
                              </v-list-item>
                              <v-list-item @click="del(item)">
                                <v-list-item-title>刪除</v-list-item-title>
                              </v-list-item>
                            </v-list>
                          </v-menu>
                        </td>
                        <td>{{item.date}}</td>
                        <td>{{item.klv}}</td>
                        <td>{{item.eat}}</td>
                        <td>{{item.hygiene}}</td>
                        <td>{{item.wc}}</td>
                        <td>{{item.bathing}}</td>
                        <td>{{item.wear}}</td>
                        <td>{{item.stool}}</td>
                        <td>{{item.pee}}</td>
                        <td>{{item.walk}}</td>
                        <td>{{item.stairs}}</td>
                        <td>{{item.bed}}</td>
                        <td>{{item.total}}</td>
                        <td>{{item.create_man}}</td>
                        <td>{{item.edit_man}}</td>
                      </tr>
                    </tbody>
                  </template>
                </v-simple-table>
              </template>
            </v-data-iterator>
          </v-card-text>
        </v-card>
      </v-card-text>
    </v-card>

    <!-- <pre>{{viewData}}</pre> -->
  </div>
</template>

<script>
import qs from "qs";
import MultiPrintSelect from "@/components/MultiPrintSelect";
import popupadd from "@/views/document/KbAdd";
import dayjs from "dayjs";

export default {
  components: { popupadd, MultiPrintSelect },
  data() {
    return {
      items: [], //目前讀取進來的資料庫
      viewData: [], //顯示的資料
      serachKey: "",
      edititem: {}, //修改中資料
      itemsPerPageOptions: [10, 20, 30],
      itemsPerPage: 10,
      printChoice: true, //設定列印選擇
      printItems:[],
    };
  },

  mounted() {
    this.getAllData();
  },

  methods: {
    //切換顯示功能
    changeChoice(){
      this.printChoice = !this.printChoice;
      this.items.filter(i=>{
        i.check = "";
      })
      this.printItems = [];
    },
    //判斷選擇準備列印的內容
    filterPrintItem(){
      if (this.printItems.length == 5){
        var pop = { msg: "單次列印指定數最多5筆!!", type: true, theme: "warning" };
        this.$store.commit("snackbar", pop);
      }
      this.printItems = this.items.filter(i=>{
        return i.check;
      })
    },
    getAllData() {
      var postData = {
        key: "u_snkey",
        value: this.$store.state.uData.snkey
      };

      //取得所有資料
      //this.$store.commit("setLoadingOn"); //讀取畫面on
      this.axios
        .post("general/getByKey/" + this.$store.state.databaseName + "/kb",qs.stringify(postData))
        .then(rs => {
          this.items = this.viewData = rs.data.map(i => {
            var newItem = JSON.parse(i.datalist);
            newItem.snkey = i.snkey;
            newItem.check = false;
            return newItem;
          });
          // this.$store.commit("setLoadingOff"); //讀取畫面off
          this.sortBy();
        });
    },
    sortBy() {
      //依name排序
      this.viewData = this.viewData.sort(function(a, b) {
        return a.date < b.date ? 1 : -1;
      });
    },
    edit(item) {
      //修改功能
      this.$refs.editProcess.$emit("fromParent",item);
    },
    del(delData) {
      //刪除功能
      this.$confirm("確認刪除?").then(res => {
        delData.delman = this.$store.state.pData.username + "(" + dayjs().format("YYYY-MM-DD HH:mm:ss") + ")";
        var postData = {
          snkey: delData.snkey,
          tablename: "kb",
          info: JSON.stringify(delData)
        };

        if (res) {
          this.axios
            .post(
              "general/delv3/" + this.$store.state.databaseName + "/kb",
              qs.stringify(postData)
            )
            .then(rs => {
              console.log(rs.data);
              if (rs.data.state == 1) {
                var pop = { msg: "已刪除", type: true, theme: "success" };
                this.$store.commit("snackbar", pop);
                this.getAllData();
              }
            });
        }
      });
    },
    search() {
      var searchArr = this.serachKey.split(" ");
      var outputitems = [...this.items];
      var str = "";
      searchArr.forEach(key => {
        if (key != "") {
          outputitems = outputitems.filter(item => {
            str = JSON.stringify(item).toUpperCase();
            if (str.includes(key.toUpperCase())) {
              return item;
            }
          });
        }
      });
      this.viewData = outputitems;
    }
  }
};
</script>


<style lang="scss">
.kblist {
  h2 {
    span {
      vertical-align: middle;
    }
  }
  thead {
    th {
      font-size: 1.2rem;
      font-family: "微軟正黑體";
      color: red;
    }
  }
  tbody {
    td {
      font-size: 1rem;
    }
  }
}
</style>